
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import UserHeader from './userHeader/userHeader'
import Scroll from '../../base/scroll/scroll'
import { mapGetters } from 'vuex'
import { userInfoMixin } from '../../assets/js/mixin'
export default {
name: "user",
mixins:[userInfoMixin],
components: {
UserHeader,
Group,
Cell,
Badge,
Scroll
},
computed:{

userSetInfo(){
return {
avatorUrl: this.userInfo.headPicId,
userName: this.userInfo.username,
typeName: (this.userInfo.isVip?'Plus会员':'普通会员'),
btnName:(this.userInfo.isVip?'续费Plus':'升级Plus')
}

}
},
data() {
return {
boxOne: [
{
title: '我的金币 32.00',
link: 'user/userCoin',
isLink: true
},
{
title: '我的资金',
link: 'user/userMoney',
isLink: true
}
],
boxTwo: [
{
title: '白拿订单',
link: 'user/noPayOrder',
isLink: true
}
],
boxThree: [
{
title: '关注商品',
link: 'user/noPayOrder',
isLink: true
},
{
title: '我的必中券',
link: 'user/userCoupons',
isLink: true
}
],
boxFour: [
{
title: '消息中心',
link: 'user/userMessage',
isLink: true
},
{
title: '账户安全',
link: 'user/settings/userSafe',
isLink: true,
isDot: true
},
{
title: '帮助与客服',
link: 'user/helpCenter',
isLink: true
},
{
title: '意见反馈',
link: 'user/userSuggest',
isLink: true,
isDot: true
}
],
boxMoney: [
{
price: 204.3,
text: '垫付本金'
},
{
price: 40.00,
text: '冻结金额'
},
{
price: 421.23,
text: '可提现金额'
}
],
boxType: [
{
price: 204.3,
text: '再一次抽奖'
},
{
price: 40.00,
text: '中奖了'
},
{
price: 421.23,
text: '评价管理'
}
],
boxOthter: [
{
icon: 'icon-gift',
badge: 1,
text: '抽奖',
type:1
},
{
icon: 'icon-winning',
text: '中奖了',
type:2
},
{
icon: 'icon-assessment',
badge: 1,
text: '评价',
type:3
},
{
icon: 'icon-reject',
badge: 1,
text: '驳回',
type:4
}
],
}
},
methods: {
arrowClick() {
this.$router.push({ name: 'settings' })
},
buyPlus() {
this.$router.push({ name: 'buyPlus' })
},
orderRouter(type){
this.$router.push({ name: 'noPayOrder',params:{checkIndex:type}})
}
}
}



